import React from "react";
import { RateLimit, Feature, CustomColumns } from "../../types";
import { useQuery } from "react-query3";
import useAppStore from "components/context/app/Hooks";
import { Modal } from "components/ui/Modal";
import { PageHeader, Space } from "antd";

const Table = React.lazy(() => import("components/ui/Table"));
const EditModal = React.lazy(() => import("components/ui/EditModal"));
const DeleteButton = React.lazy(() => import("components/ui/DeleteButton"));
const EditorButton = React.lazy(() => import("components/ui/EditorButton"));
const NotLicensed = React.lazy(() => import("../pages/notLicensed"));
const RateLimitModal = React.lazy(() => import("./RateLimitModal"));
const RateLimitSettingsModal = React.lazy(
  () => import("./RateLimitSettingsModal")
);

export default function RateLimits() {
  const { licensed } = useAppStore();
  const isLicensed = licensed(Feature.Api);

  const { data: ratelimits, isLoading, refetch } = useQuery<RateLimit[]>(
    "/ratelimit",
    {
      enabled: false,
    }
  );

  const columns: CustomColumns<RateLimit>[] = [
    {
      key: "endpoint",
      title: "Endpoint",
      dataIndex: "endpoint",
    },
    {
      key: "limit",
      title: "Limit",
      dataIndex: "limit",
    },
    {
      key: "period",
      title: "Period",
      dataIndex: "period",
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      render: (text, record) => {
        return (
          <Space>
            <Modal>
              <EditModal record={record} allowedWithOneWayGitSync={false} />
            </Modal>
            <EditorButton to={`${record?.id}`} allowedWithOneWayGitSync={false} />
            <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
          </Space>
        );
      },
    },
  ];

  return isLicensed ? (
    <PageHeader title="Rate Limits" subTitle="Rate limits for the PowerShell Universal Web API.">
      <Table
        title="Rate Limits"
        actions={[
          <Modal>
            <RateLimitModal />
          </Modal>,
          <Modal>
            <RateLimitSettingsModal />
          </Modal>,
        ]}
        columns={columns}
        loading={isLoading}
        data={ratelimits}
        queryFn={refetch}
      />
    </PageHeader>

  ) : (
    <NotLicensed title="Rate limits are only available for licensed instances." />
  );
}
